
<template>
  <div class="cx-image-scale" @doubleTap="handleDblClick" @dblclick="handleDblClick" ref="imgWrap">
    <canvas ref="bargraphCanvas" :width="canvasWidth" :height="canvasHeight"></canvas>
  </div>
</template>

<script>
export default {
  name: 'CxImage',
  props: {
    src: {
      type: String,
      default: '',
    }
  },
  data () { 
    return {
      canvasWidth: 600,
      canvasHeight: 600,
      myCanvas: null,
      ctx: null,
      imgX: 0, // x
      imgY: 0,
      MINIMUM_SCALE: 0.2,
      imgScale: 1, // 
      loadImgObj: null,
      img: null,
      flag: false,
      pos: {},
      posl: {},
      flagPC: true,
      scaleSpeed: 0.1,
    }
  },
  mounted() {
    this.canvasWidth = this.$refs.imgWrap.offsetWidth
    this.canvasHeight = this.$refs.imgWrap.offsetHeight
    this.flagPC = this.IsPC()
    this.myCanvas = this.$refs.bargraphCanvas
    this.ctx = this.myCanvas.getContext('2d')
    this.src && this.loadImg()
  },
  watch: {
    src(){
      this.initData()
      this.loadImg()
    }   
  },
  methods: {
    loadImg() {
      let _this = this
      _this.img = new Image()
      _this.img.src = this.src
      _this.img.onload = function () {
        _this.drawImage()
        _this.canvasEventsInit()
      }
      !_this.src &&  _this.drawImage()
    },
    drawImage() {
      let _this = this
      this.ctx.clearRect(0, 0, this.myCanvas.width, this.myCanvas.height)
      this.ctx.drawImage(
        _this.img, //
        0, 0, // x 
        _this.img.width, _this.img.height,  //
        _this.imgX, _this.imgY,// x y
        _this.img.width * _this.imgScale, _this.img.height * _this.imgScale  //
      )
    },
    canvasEventsInit() {
      var pageX, pageY, initX, initY
      var start = []
      let _this = this
      if(this.flagPC) {//PC
        this.myCanvas.onmousedown = function (event) {
          _this.flag = true
          _this.pos = _this.windowToCanvas(event.clientX, event.clientY) //canvas
        }
        this.myCanvas.onmousemove = function (evt) {  //
          if(_this.flag ){
            _this.posl = _this.windowToCanvas(evt.clientX, evt.clientY)
            var x = _this.posl.x - _this.pos.x, y = _this.posl.y - _this.pos.y
            _this.imgX  += x
            _this.imgY  += y
            _this.pos = JSON.parse(JSON.stringify(_this.posl))
            _this.drawImage()//
          }
        }
        this.myCanvas.onmouseup = function () {
          _this.flag  = false
        }
        this.myCanvas.onmousewheel = this.myCanvas.onwheel = function (event) {    //
          var pos = _this.windowToCanvas(event.clientX, event.clientY)
          var newPos = {x:((pos.x-_this.imgX)/_this.imgScale).toFixed(2) , y:((pos.y-_this.imgY)/_this.imgScale).toFixed(2)}
          if (event.wheelDelta > 0) {// 
                  _this.imgScale += _this.scaleSpeed
                  _this.imgX = (1-_this.imgScale)*newPos.x+(pos.x-newPos.x)
                  _this.imgY = (1-_this.imgScale)*newPos.y+(pos.y-newPos.y)
          } else {//  
              _this.imgScale -= _this.scaleSpeed
              if(_this.imgScale<_this.MINIMUM_SCALE) {//1
                  _this.imgScale = _this.MINIMUM_SCALE
              }
              _this.imgX = (1-_this.imgScale)*newPos.x+(pos.x-newPos.x)
              _this.imgY = (1-_this.imgScale)*newPos.y+(pos.y-newPos.y)
          }
          _this.drawImage()  //
          event.preventDefault()
        }
      } else { //Pad
        this.myCanvas.addEventListener('touchstart', function (event) {
          _this.flag = true
          if(event.touches && event.touches.length < 2) {
            let touch = event.touches[0]
            _this.pos = _this.windowToCanvas(touch.clientX, touch.clientY) //canvas
          }else{
            let touches = event.touches
            //XY  
            pageX = touches[0].pageX
            pageY = touches[0].pageY
            //XY   
            initX = event.target.offsetLeft
            initY = event.target.offsetTop
            console.log(pageX,pageY,initX,initY)
            //  
            if (touches.length >= 2) { //
              start = touches//
            }
          }
        })
        this.myCanvas.addEventListener('touchmove', function (evt) {  //
          if(_this.flag ){
            if(evt.touches && evt.touches.length < 2) {
              let touch = evt.touches[0]
              _this.posl = _this.windowToCanvas(touch.clientX, touch.clientY)
              var x = _this.posl.x - _this.pos.x, y = _this.posl.y - _this.pos.y
              _this.imgX  += x
              _this.imgY  += y
              _this.pos = JSON.parse(JSON.stringify(_this.posl))
            }else{
              let touches = evt.touches
              // 2   
              //
              var now = touches
              var pos = _this.windowToCanvas (now[0].clientX, now[0].clientY)
              var newPos = {x:((pos.x-_this.imgX)/_this.imgScale).toFixed(2) , y:((pos.y-_this.imgY)/_this.imgScale).toFixed(2)}
              // getDistance 
              if(_this.getDistance(now[0], now[1]) < _this.getDistance(start[0], start[1])){
                // 
                _this.imgScale -= _this.scaleSpeed
                if(_this.imgScale<_this.MINIMUM_SCALE) {//1
                  _this.imgScale = _this.MINIMUM_SCALE
                }
                _this.imgX = (1-_this.imgScale)*newPos.x+(pos.x-newPos.x)
                _this.imgY = (1-_this.imgScale)*newPos.y+(pos.y-newPos.y)
              }else if(_this.getDistance(now[0], now[1]) > _this.getDistance(start[0], start[1])){
                // 
                _this.imgScale += _this.scaleSpeed
                _this.imgX = (1-_this.imgScale)*newPos.x+(pos.x-newPos.x)
                _this.imgY = (1-_this.imgScale)*newPos.y+(pos.y-newPos.y)
              }
              start = now
            }
            _this.drawImage() //
          }
        })
        this.myCanvas.addEventListener('touchend', function () {
          _this.flag  = false
        })
     }
    },
    /**/
    windowToCanvas(x,y) {
      var box = this.myCanvas.getBoundingClientRect()//lefttoprightbottom
      return {
        x: x - box.left - (box.width - this.myCanvas.width) / 2,
        y: y - box.top - (box.height - this.myCanvas.height) / 2
      }
    },
    // -
    getDistance(p1, p2) {
      var x = p2.pageX - p1.pageX,
      y = p2.pageY - p1.pageY
      return Math.sqrt((x * x) + (y * y))
    },
    IsPC() {
      var userAgentInfo = navigator.userAgent
      var Agents = ["Android", "iPhone",
                  "SymbianOS", "Windows Phone",
                  "iPad", "iPod", "QtWebEngine"]
      var flag = true
      for (var v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = false
          break
        }
      }
      return flag
    },
    handleDblClick () { //
      this.initData()
      this.src && this.drawImage() //
    },
    initData () { //src
      this.imgScale = 1
      this.imgX = 0
      this.imgY = 0
    }
  }
}
</script>

<style scoped lang="scss">
.cx-image-scale {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  background: #001;
  &::-webkit-scrollbar  
  {  
    width: 0;  /**/
    height: 0;  /**/
  }  
  /* +*/  
  &::-webkit-scrollbar-track  
  {  
    -webkit-box-shadow: inset 0 0 0 rgba(0,0,0,0.3);  
    border-radius: 0;  /**/
  }  
  /* +*/  
  &::-webkit-scrollbar-thumb  
  {  
    border-radius: 0;  /**/
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
  }
 }
</style>